import { useRef, useState, useMemo } from "react";
import axios from 'axios';
import ReactQuill, { Quill } from "react-quill";
import "react-quill/dist/quill.snow.css";
import styled from "styled-components";
import { useNavigate } from "react-router-dom";
import { API_BASE_URL } from "./apis";


const Image = Quill.import("formats/image")
Image.sanitize = (url) => url;

const EditorWrapper = styled.div`
    display: flex;
    flex: 1;
    flex-direction: column;
    margin: 0 1.5rem 1.5rem 1.5rem;
    gap: 1rem;
    overflow: hidden;
`
const TitleWrapper = styled.div`
  display: flex;
  gap: 0.5rem;
  input {
    font-size: 1.25rem;
    padding: 0.75rem;
    border: none;
    border-radius: 0.5rem;
    background-color: #f7f7f7;
  }
  #title{
    width: 100%;
  }
  #creator{
    width: 8rem;
  }
`

const EditorComponent = () => {
  const QuillRef = useRef();
  const [contents, setContents] = useState("");
  const [title, setTitle] = useState("");
  const [creator, setCreator] = useState("");
  const navigate = useNavigate();



  const imageHandler = () => {


    const input = document.createElement('input');

    input.setAttribute('type', 'file');
    input.setAttribute('accept', 'image/*');
    input.click();

    input.addEventListener('change', async () => {
      console.log('');
      const file = input.files[0];
      const formData = new FormData();
      formData.append('img', file);

      try {
        // const result = await axios.post('http://localhost:8000/image', formData);
        // const IMG_URL = result.data.url;

        //  URL img  src          
        // src base64   URL        
        //     uploads      URL .

        //     -   .
        const editor = QuillRef.current.getEditor(); //   
        // 1.  root innerHTML 
        // editor root   .  img .
        //   ->    URL  ->      .
        // editor.root.innerHTML =
        //   editor.root.innerHTML + `<img src=${IMG_URL} /><br/>`; //     .

        // 2.     
        const range = editor.getSelection();
        //    
        const imageUrl = URL.createObjectURL(file);
        editor.insertEmbed(range.index, 'image', imageUrl);
      } catch (error) {
        console.log(error);
      }
    });
  };

  async function submit(e) {
    e.preventDefault();
    
    const formData = new FormData();

    formData.append('title', title);
    formData.append('creator', creator);

    //Promise.all()     .

    const images = await Promise.all(
      contents.ops
        .filter((op) => op.insert?.image)
        .map(async (op) => ({
          blobUrl: op.insert.image,
          imageFile: await fetch(op.insert.image).then(r => r.blob())
        })),
    );
    images.forEach(({ blobUrl, imageFile }) => {
      formData.append(blobUrl, imageFile);
    });

    formData.append('contents', JSON.stringify(contents));

    console.log(formData);
    axios.post(`${API_BASE_URL}/apis/posts`, formData)
    .then((response)=>navigate('/posts/'+ response.data.id))
  }
  function handleChange(content, delta, source, editor) {
    setContents(editor.getContents());
  }
// quill     .
//    ,    .
// useMemo  ,   , imageHandler  focus   .
  const modules = useMemo(
    () => ({
      toolbar: {
        container: [
          ["bold", "italic", "underline", "strike", "blockquote"],
          [{ size: ["small", false, "large", "huge"] }, { color: [] }],
          [
            { list: "ordered" },
            { list: "bullet" },
            { indent: "-1" },
            { indent: "+1" },
            { align: [] },
          ],
          ["image"],
          [{ header: [1, 2, 3, false] }],
        ],
        handlers: {
          image: imageHandler,
        },
      },
    }),
    []
  );
  const formats = [
    'header',
    'bold',
    'italic',
    'underline',
    'strike',
    'blockquote',
    'image',
  ];

  return (
    <EditorWrapper>
      <TitleWrapper>
        <input 
          id="creator"
          onChange={(e) => { setCreator(e.target.value) }} 
          defaultValue={creator ? creator: ''}
          placeholder=""
        ></input>
        <input 
          id="title"
          onChange={(e) => { setTitle(e.target.value)}} 
          value={title}
          placeholder=" ."
        ></input>
      </TitleWrapper>
        <ReactQuill
          ref={(element) => {
            if (element !== null) {
              QuillRef.current = element;
            }
          }}
          value={contents}
          onChange={handleChange}
          modules={modules}
          theme="snow"
          placeholder=" ."
          formats={formats}
          style={{flex:'1',display:'flex',flexDirection:'column',overflow:'hidden'}}
        />
        <button className="btn" onClick={submit}> </button>
    </EditorWrapper>
  )
}
export default EditorComponent;